<template>
  <div class="contentPage">
    <div class="contentBorder">
      <el-row class="topAdd">
        <el-col style="width: 300px" class="queryinp">
          <div>
            <el-input
              placeholder=""
              v-model="query.userName"
              size="small"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="getRoleTab"
              ></el-button>
            </el-input>
          </div>
        </el-col>
        <el-button type="primary" size="small" @click="addrole"
          ></el-button
        >
      </el-row>

      <el-row class="rolecontent">
        <el-col
          class="roleTab"
          v-loading="tabLoading"
          element-loading-text=""
        >
          <div>
            <el-table
              :data="roleTabData"
              stripe
              border
              style="width: 100%"
              :height="$store.getters.docHeight"
            >
              <el-table-column
                :resizable="false"
                type="index"
                width="60"
                label=""
                align="center"
              ></el-table-column>
              <el-table-column
                :resizable="false"
                prop="userName"
                label=""
                width="160"
                align="center"
              ></el-table-column>
              <el-table-column
                :resizable="false"
                prop="realName"
                label=""
                width="160"
                align="center"
              ></el-table-column>
              <el-table-column
                :resizable="false"
                prop="updateName"
                label=""
                width="160"
                align="center"
              ></el-table-column>
              <el-table-column
                :resizable="false"
                prop="status"
                label=""
                :formatter="Fstatus"
                width="160"
                align="center"
              ></el-table-column>
              <el-table-column
                :resizable="false"
                prop="remark"
                label=""
                align="center"
              ></el-table-column>
              <el-table-column
                :resizable="false"
                fixed="right"
                label=""
                align="center"
                width="315"
              >
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    type="primary"
                    @click="allocation(scope.row)"
                    ></el-button
                  >
                  <el-button
                    size="mini"
                    type="success"
                    @click="updateAuth(scope.row)"
                    ></el-button
                  >
                  <el-button
                    size="mini"
                    type="danger"
                    @click="cancelBtn(scope.row, 1)"
                    ></el-button
                  >
                  <el-button
                    v-if="scope.row.status == 1"
                    size="mini"
                    type="primary"
                    @click="cancelBtn(scope.row, 2)"
                    ></el-button
                  >
                  <el-button
                    v-if="scope.row.status == 2"
                    size="mini"
                    type="success"
                    @click="cancelBtn(scope.row, 3)"
                    ></el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-col>
      </el-row>

      <div class="tabbottom">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="query.page"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="query.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
    </div>

    <!--  -->
    <el-dialog
      title=""
      @close="emptyForm"
      :visible.sync="dialogFormVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <el-form
        :model="addForm"
        :rules="rules"
        ref="addForm"
        label-width="80px"
        label-position="right"
      >
        <el-form-item label="" prop="userName">
          <el-input
            v-model="addForm.userName"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="realName">
          <el-input
            v-model="addForm.realName"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            style="width: 100%"
            :options="originTree"
            :props="defaultProps"
            @change="selectOrg"
            change-on-select
            :show-all-levels="false"
            v-model="parent"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="roleId">
          <el-select
            style="width: 100%"
            v-model="addForm.roleId"
            placeholder=""
          >
            <el-option
              v-for="item in allRole"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select
            style="width: 100%"
            v-model="addForm.status"
            placeholder=""
          >
            <el-option
              v-for="item in userStatus"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input
            type="textarea"
            v-model="addForm.remark"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="subForm" :loading="subBtnLonging"
          > </el-button
        >
      </div>
    </el-dialog>
    <!--  -->
    <!--  -->
    <el-dialog
      :visible.sync="dialogform"
      style="width: 100%"
      @close="emptyroleForm"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <div style="text-align: center; margin-top: -44px; font-size: 22px">
        
        <span style="color: red">{{ thisRow.userName }}</span>
      </div>
      <el-button size="mini" type="primary" @click="isShowForm = true"
        ></el-button
      >

      <el-table
        highlight-current-row
        border
        style="width: 100%; margin-top: 10px"
        :data="roleAllotTab"
      >
        <el-table-column
          :resizable="false"
          prop="orgName"
          label=""
        ></el-table-column>
        <el-table-column
          :resizable="false"
          prop="roleName"
          label=""
        ></el-table-column>
        <el-table-column
          :resizable="false"
          fixed="right"
          label=""
          width="210"
        >
          <template slot-scope="scope">
            <el-button size="mini" type="danger" @click="deleteRole(scope.row)"
              ></el-button
            >
            <el-button size="mini" type="primary" v-if="scope.row.mainOrg == 1"
              ></el-button
            >
            <el-button
              size="mini"
              type="primary"
              @click="mainRole(scope.row)"
              v-else
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <el-form
        :inline="true"
        :model="roleForm"
        :rules="rules"
        ref="roleForm"
        style="margin-top: 20px"
        label-width="80px"
        v-if="isShowForm"
        label-position="right"
      >
        <el-form-item label="">
          <el-cascader
            :options="originTree"
            :props="defaultProps"
            @change="selectOrg"
            change-on-select
            :show-all-levels="false"
            v-model="parent"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="roleId">
          <el-select v-model="roleForm.roleId" placeholder="">
            <el-option
              v-for="item in allRole"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button
            style="margin-left: 30px"
            type="primary"
            size="mini"
            @click="addRole"
            ></el-button
          >
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--  -->
  </div>
</template>

<script>
import { _debounce } from "@/libs/public.js";
import zTree from "ztree";
import "ztree/css/metroStyle/metroStyle.css";
export default {
  data () {
    let checkid = function (rule, value, callback) {
      if (value === "") {
        return callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      userStatus: [
        {
          //
          label: "",
          value: 1
        },
        {
          label: "",
          value: 2
        }
      ],
      userFstatus: ["", "", ""], //
      isAction: "", //   
      query: {
        //
        page: 1,
        pageSize: 20,
        // userName: "",
        // realName: "",
        // orgId: ""
      },
      tabLoading: false, //
      roleTabData: [], //
      total: 0, //
      dialogFormVisible: false, //
      dialogform: false, //
      addForm: {
        //
        id: '',
        userName: "",
        realName: "",
        status: "",
        orgIds: "",
        roleIds: "",
        remark: ""
      },
      subBtnLonging: false, //
      roleAllotTab: [], //
      thisRow: {}, //
      originTree: [], //
      roleForm: {
        // 
        userId: "",
        orgId: "",
        roleId: ""
      },
      allRole: [], //
      parent: [], //
      defaultProps: {
        label: "name",
        children: "children",
        value: "id"
      },
      isShowForm: false, //
      rules: {
        userName: [
          { required: true, message: "", trigger: "blur" }
        ],
        realName: [{ required: true, message: "", trigger: "blur" }],
        status: [{ required: true, validator: checkid, trigger: "change" }],
        roleId: [{ required: true, validator: checkid, trigger: "change" }]
      }
    };
  },
  created () {
    this.getorgsTree();
    this.getAllRole();
  },
  mounted () {
    this.getRoleTab();
  },
  methods: {
    getAllRole () {
      //
      this.$fetchGet("roles/idAndName").then(res => {
        this.allRole = res;
      });
    },
    getorgsTree () {
      //
      this.$fetchGet("orgs/getTree").then(res => {
        let originTree = this.parseChildren(1, res.slice(0));
        this.deleteChildren(originTree);
        this.originTree = originTree;
      });
    },
    getRoleTab () {
      //
      this.tabLoading = true;
      this.$fetchGet("users", this.query).then(res => {
        this.tabLoading = false;
        this.roleTabData = res.list;
        this.total = res.total;
      });
    },
    getNewRole () {
      //
      this.$fetchGet("users/getRoleByUser/" + this.roleForm.userId).then(
        res => {
          this.roleAllotTab = res;
        }
      );
    },
    addRole () {
      //
      if (this.roleForm.orgId == "") {
        this.Gwarning("");
        return;
      }
      this.$refs.roleForm.validate(valid => {
        if (valid) {
          this.$fetchPost("users/saveOrgAndRole", this.roleForm).then(res => {
            if (res == 1) {
              this.Gsuccess("");
              this.getNewRole();
            } else {
              this.Gerror("", +res.message);
            }
          });
        }
      });
    },
    deleteRole (row) {
      //
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$fetchDelete("users/deleteOrgAndRole", {
          userId: row.userId,
          orgId: row.orgId,
          roleId: row.roleId
        }).then(res => {
          if (res == 1) {
            this.Gsuccess("");
            this.getNewRole();
          } else {
            this.Gerror("", +res.message);
          }
        });
      });
    },
    mainRole (row) {
      //
      this.$fetchPut("roles/signMainOrg", {
        userId: row.userId,
        orgId: row.orgId,
        roleId: row.roleId
      }).then(res => {
        if (res.status == 0) {
          this.Gsuccess("");
          this.getNewRole();
        } else {
          this.Gerror("", +res.message);
        }
      });
    },
    allocation (row) {
      //
      this.dialogform = true;
      this.roleForm.userId = row.id;
      this.thisRow = row;
      this.getNewRole();
    },
    addrole () {
      //
      this.dialogFormVisible = true;
      this.isAction = "add";
    },
    updateAuth (row) {
      //
      $.each(this.addForm, (key, item) => {
        this.addForm[key] = row[key];
      });
      this.isAction = "update";
      this.dialogFormVisible = true;
      this.addForm.id = row.id;
    },
    cancelBtn (row, type) {
      //
      let str, url;
      switch (type) {
        case 1:
          str = "";
          url = "users/cancelUser/";
          break;
        case 2:
          str = "";
          url = "users/freezeUser/";
          break;
        case 3:
          str = "";
          url = "users/unFreezeUser/";
          break;
      }
      this.$confirm(`${str}`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$fetchPut(url + row.id).then(res => {
          if (res == "success") {
            this.Gsuccess(`${str}`);
            this.getRoleTab();
          } else {
            this.Gerror(`${str}`);
          }
        });
      });
    },
    freezeBtn (row) {
      //
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$fetchPut("users/freezeUser/" + row.id).then(res => {
          if (res == "success") {
            this.Gsuccess("");
            this.getRoleTab();
          } else {
            this.Gerror("");
          }
        });
      });
    },
    subForm: _debounce(function () {
      //
      this.$refs.addForm.validate(valid => {
        if (valid) {
          let formData = this.cloneObj(this.addForm);
          this.subBtnLonging = true;
          if (this.isAction === "add") {
            this.$fetchPost("users", formData).then(res => {
              if (res) {
                this.Gsuccess("");
                this.dialogFormVisible = false;
                this.getRoleTab();
              } else {
                this.Gerror(res.message);
              }
              this.subBtnLonging = false;
            });
          } else if (this.isAction === "update") {
            this.$fetchPut("users", formData).then(res => {
              if (res) {
                this.Gsuccess("");
                this.dialogFormVisible = false;
                this.getRoleTab();
              } else {
                this.Gerror(res.message);
              }
              this.subBtnLonging = false;
            });
          }
        }
      });
    }, 300),
    emptyForm () {
      //
      this.addForm = {
        userName: "",
        realName: "",
        status: "",
        remark: ""
      };
      this.$refs.addForm.resetFields();
    },
    emptyroleForm () {
      this.roleForm = {
        userId: "",
        orgId: "",
        roleId: ""
      };
      if (this.isShowForm) {
        this.$refs.roleForm.resetFields();
        this.isShowForm = false;
      }
    },
    selectOrg (val) {
      //
      if (val.length > 0) {
        this.addForm.orgId = val[val.length - 1];
        this.roleForm.orgId = val[val.length - 1];
      }
    },
    handleSizeChange (val) {
      // 
      this.query.pageSize = val;
      this.getRoleTab();
    },
    handleCurrentChange (val) {
      // 
      this.query.page = val;
      this.getRoleTab();
    },
    Fstatus (row) {
      //
      return this.userFstatus[row.status];
    },
    parseChildren (pid, json) {
      //
      var arr = [];
      $.each(json, (index, item) => {
        // item.Ids = item.pid + "," + item.id;
        if (item.pid === pid) {
          arr.push(item);
          item.children = this.parseChildren(item.id, json);
        }
      });
      return arr;
    },
    deleteChildren (menuData) {
      //child
      $.each(menuData, (index, item) => {
        if (item.children && item.children.length) {
          this.deleteChildren(item.children);
        } else {
          delete item.children;
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.contentPage {
  background: #e3e9f3;
  .contentBorder {
    // width: 100%;
    height: 100%;
    position: relative;
    .topAdd {
      text-align: right;
      width: 100%;
      height: 50px;
      background: #fff;
      padding: 10px;
      box-sizing: border-box;
    }
    .rolecontent {
      padding-top: 10px;
      box-sizing: border-box;
      .roleTab {
        background-color: #fff;
      }
    }
  }
}
</style>

