const app = {
  state: {
    docHeight: $(document).height() - 180, //
    menuFlag: false, //
    
  },
  mutations: {
    SET_FLAG: (state) => { //
      state.menuFlag = !state.menuFlag
    },
    SET_HEIGHT: (state, docHeight) => { //
      state.docHeight = docHeight
    }
  }
}
export default app
